import { Component, Input } from '@angular/core';

@Component({
  selector: 'app-default',
  templateUrl: './default.component.html',
  styleUrls: ['./default.component.scss']
})
export class DefaultComponent {
  @Input() data_all: Array<any>=[];

  @Input() congregation_name: string ='';

  choice(value:any){
    let loop=true
    for(let key in value){
        switch(key){
            case'CONGREGAÇÃO':
            case'Cogregação':
            case'congregação':
            case'CONG':
            case'Cong':
            case'cong':
                value = key
                loop=false
            break;
            default:
                value=""
            break;
                
        }
        if(!loop)break
    }
    return value
}
}
