import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CurrentUserService {
  private current_user = new Subject<any>();
  private component_hide = new Subject<any>();
  user$ = this.current_user.asObservable();
  component_hide$ = this.component_hide.asObservable();
  prepareUser(user: any){
    this.current_user.next(user);
  }

  componentHide(isHide: boolean){
    this.component_hide.next(isHide);
  }
}
